"use client"
import React, { useState } from 'react'
import Image from 'next/image'
import { team } from '../content/site'

export default function TeamList() {
  const [open, setOpen] = useState<string | null>(null)
  return (
    <div className="mt-6 grid grid-cols-1 sm:grid-cols-2 gap-6">
      {team.map((m) => (
        <article key={m.id} className="bg-neutral-900/50 border border-neutral-800 p-4 rounded shadow-sm">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 rounded bg-neutral-800 flex items-center justify-center text-xs font-medium">{m.name.split(' ').map(n=>n[0]).join('')}</div>
            <div className="flex-1">
              <div className="font-medium">{m.name}</div>
              <div className="text-xs text-neutral-400">{m.role}</div>
            </div>
            <button className="text-sm text-accent" onClick={() => setOpen(open === m.id ? null : m.id)}>{open === m.id ? 'Hide' : 'Read'}</button>
          </div>
          {open === m.id && (
            <div className="mt-3 text-neutral-300">Placeholder bio for {m.name}. This is a concise, professional summary of their background, role, and work with Resonaré Chamber Choir. Replace with real copy when available.</div>
          )}
        </article>
      ))}
    </div>
  )
}
